import React from 'react';
import { Panel, ListItem, Button, Group, Div, Avatar, PanelHeader, List, Cell, FormLayout,FormLayoutGroup,Input,Link,Spinner } from '@vkontakte/vkui';
import connect from '@vkontakte/vkui-connect';

class Home extends React.Component {

	state = {
		data: null,
		inputValue: null,
		url: null
	};

	constructor(props){
		super(props);
		this.ShortLinkList = this.ShortLinkList.bind(this);
		this.ShortLink = this.ShortLink.bind(this);
		this.updateInput = this.updateInput.bind(this)
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppCallAPIMethodResult':
					console.log(e.detail.data.response);
					if (e.detail.data.response.items){
						this.setState({ data: e.detail.data.response });
					} else if (e.detail.data.response.short_url) {
						this.setState({url: e.detail.data.response.short_url})
					}
					break;
				default:
					console.log(e.detail);
			}
		});
		this.ShortLinkList()
	}

	ShortLinkList(){
		connect.send(
			"VKWebAppCallAPIMethod",
			{
				"method": "utils.getLastShortenedLinks",
				"params": {
					"v": 5.92,
					"access_token": this.props.token
				}
			});
	}

	ShortLink() {
		alert(this.props.token);

		connect.send(
			"VKWebAppCallAPIMethod",
			{
				"method": "utils.getShortLink",
				"params": {
					"url": this.state.inputValue,
					"v": 5.92,
					"access_token": this.props.token
				}
			});
		this.setState({inputValue: null})
	}
	updateInput (evt){
		this.setState({
			inputValue: evt.target.value
		});
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>Ссылки</PanelHeader>
				<FormLayout>
					<FormLayoutGroup top="Ссылка">
						<Input type="text" value={this.state.inputValue} onChange={this.updateInput}/>
						<Button size='xl' level="secondary" onClick={this.ShortLink}>Сократить</Button>
					</FormLayoutGroup>
				</FormLayout>
				{this.props.fetchedUser &&
				<Group>
					<ListItem
						before={this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null}
						description={this.props.fetchedUser.city && this.props.fetchedUser.city.title ? this.props.fetchedUser.city.title : ''}
					>
						{`${this.props.fetchedUser.first_name} ${this.props.fetchedUser.last_name}`}
					</ListItem>
				</Group>}
				<Group title="Ссылки">
					<Div>
						{this.state.data ?

						<Group>
							<List>
								{
									this.state.url ?
										<Cell>
											<Link href={this.state.url}>{this.state.url}</Link>
										</Cell>
									:
									null
								}
								{this.state.data.items.map(function(item, i){
									return <Cell key={i}>
										<Link href={item.short_url}>{item.short_url}</Link>
									</Cell>;
								})}
							</List>
						</Group> :
							<Group>
								<div style={{ display: 'flex', alignItems: 'center', flexDirection: 'column' }}>
									<Spinner size="large" style={{ marginTop: 20 }} />
								</div>
							</Group>
						}
					</Div>
				</Group>
			</Panel>
		)
	}
}

export default Home;
